'use strict';

// MODULES //

var setReadOnly = require( '@stdlib/utils-define-nonenumerable-read-only-property' );
var isNumber = require( '@stdlib/assert-is-number' ).isPrimitive;
var isnan = require( '@stdlib/math-base-assert-is-nan' );
var isNonNegativeInteger = require( '@stdlib/assert-is-nonnegative-integer' ).isPrimitive;
var iteratorSymbol = require( '@stdlib/symbol-iterator' );
var format = require( '@stdlib/string-format' );


// VARIABLES //

var DEFAULT_LENGTH = 1e308;


// MAIN //

/**
* Returns an iterator which returns a sequence of numbers according to a specified increment.
*
* @param {number} start - starting value (inclusive)
* @param {number} increment - increment
* @param {number} [N=1e308] - number of values
* @throws {TypeError} first argument must be a number
* @throws {TypeError} second argument must be a number
* @throws {TypeError} third argument must be a nonnegative integer
* @returns {Iterator} iterator
*
* @example
* var iter = iterStep( 0, 2, 10 );
*
* var v = iter.next().value;
* // returns 0
*
* v = iter.next().value;
* // returns 2
*
* v = iter.next().value;
* // returns 4
*
* // ...
*/
function iterStep( start, increment, N ) {
	var iter;
	var FLG;
	var n;
	var i;

	if ( !isNumber( start ) || isnan( start ) ) {
		throw new TypeError( format( 'invalid argument. First argument must be a number. Value: `%s`.', start ) );
	}
	if ( !isNumber( increment ) || isnan( increment ) ) {
		throw new TypeError( format( 'invalid argument. Second argument must be a number. Value: `%s`.', increment ) );
	}
	if ( arguments.length > 2 ) {
		if ( !isNonNegativeInteger( N ) ) {
			throw new TypeError( format( 'invalid argument. Third argument must be a nonnegative integer. Value: `%s`.', N ) );
		}
		n = N;
	} else {
		n = DEFAULT_LENGTH;
	}
	i = -1;

	// Create an iterator protocol-compliant object:
	iter = {};
	setReadOnly( iter, 'next', next );
	setReadOnly( iter, 'return', end );

	// If an environment supports `Symbol.iterator`, make the iterator iterable:
	if ( iteratorSymbol ) {
		setReadOnly( iter, iteratorSymbol, factory );
	}
	return iter;

	/**
	* Returns an iterator protocol-compliant object containing the next iterated value.
	*
	* @private
	* @returns {Object} iterator protocol-compliant object
	*/
	function next() {
		i += 1;
		if ( FLG || i >= n ) {
			return {
				'done': true
			};
		}
		return {
			'value': start + (i*increment),
			'done': false
		};
	}

	/**
	* Finishes an iterator.
	*
	* @private
	* @param {*} [value] - value to return
	* @returns {Object} iterator protocol-compliant object
	*/
	function end( value ) {
		FLG = true;
		if ( arguments.length ) {
			return {
				'value': value,
				'done': true
			};
		}
		return {
			'done': true
		};
	}

	/**
	* Returns a new iterator.
	*
	* @private
	* @returns {Iterator} iterator
	*/
	function factory() {
		return iterStep( start, increment, n );
	}
}


// EXPORTS //

module.exports = iterStep;
